<template>
     <div>
       <ul>
         <li v-for="news in newsList" :key="news.id">
           <router-link :to="{ name: 'news-detail', params: { id: news.id } }">
             {{ news.title }}
           </router-link>
         </li>
       </ul>
     </div>
   </template>
   
   <script>
   import axios from 'axios';
   
   export default {
     data() {
       return {
         newsList: [],
       };
     },
     created() {
       this.fetchNewsList();
     },
     methods: {
       async fetchNewsList() {
         try {
           const response = await axios.get('/api/news');
           this.newsList = response.data;
         } catch (error) {
           console.error(error);
         }
       },
     },
   };
   </script>
   